function Detail(props){

    //hook
    //componenet가 mount , update시 실행
    //시간이 좀 오래걸리는 연산, 서버에서 데이터를 받아올 때, Timer등에 사용
    //Dependency에 모양만 만들어두면 1회만 동작
    // useEffect( ()=> {       
    //     let Timer = setTimeout( ()=>{ setAlertVal(1) } , 2000)
        
    //     //Clean Up Function
    //     //서버로 데이터 요청 받아오고 할때 쓰면 좋을듯
    //     //mount 될때는 실행 안됨
    //     //unmount 될때는 실행 함
    //     return()=>{
    //         //기존의 타이머는 제거해주세욧 
    //         //기존의 데이터 요청은 제거해주세욧
    //         clearTimeout(Timer);
    //     }    
    // }, [])

    //redux store의 모든 state 꺼내서 a에 저장
    //useSelector에 함수 필수 
    //store.js에 요청을 보내는 함수
    let dispatch =  useDispatch();

    let [num, setNum] = useState('');

    let [count, setCount] = useState(0);
    let [alertVal, setAlertVal] = useState(0);

    let [tab, setTab] = useState(0);

    let {id} = useParams();

    let findResult = props.product.findIndex(function(x){       
        return x.id == id;
    })

    useEffect( () => {
        //상품 id 가져오고
        //localstorage에 넣어라
        console.log('ddddddddd '+id);
        let output = localStorage.getItem('recentView')
        output = JSON.parse(output);
        output.push(props.product[findResult].id);
        //중복제거 set자료형
        output = new Set(output);
        output = Array.from(output);
        localStorage.setItem('recentView', JSON.stringify(output));
    }, [])

    return(
        <div className="container">
            {/* <YellowBtn bg="skyblue">버튼</YellowBtn> */}

            {/* {
                alertVal == 1 ? null : <div className="alert alert-warning">2초 이내 눌럿</div>                
            } */}

            <div className="row">
                <div className="col-md-6">
                    <img src = {props.itemImg[findResult]} width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                    <div>
                        {/* <input onChange={(e)=>{setNum(e.target.value)}}></input> */}
                    </div>
                    <h4>{props.product[findResult].title}</h4>
                    <p>{props.product[findResult].content}</p>
                    <p>KRW {props.product[findResult].price}</p>
                    <button onClick={ ()=>{ 
                        dispatch(addProduct( {id: 1, name:props.product[findResult].title, count: props.product[findResult].count} ));
                    } }>구매하기</button>
                </div>
            </div>            

            <Nav variant="tabs" defaultActiveKey="link0">
            <Nav.Item>
                <Nav.Link eventkey="link0" onClick={()=>{setTab(0)}}>Active</Nav.Link>
            </Nav.Item>
            <Nav.Item>
                <Nav.Link eventKey="link1" onClick={()=>{setTab(1)}}>Option 2</Nav.Link>
            </Nav.Item>
            <Nav.Item>
                <Nav.Link eventKey="link2" onClick={()=>{setTab(2)}}>Disabled</Nav.Link>
            </Nav.Item>
            </Nav>

            <TabContent tab={tab}/>

        </div>
        
    )
}

//--- Array Tip 1
function TabContent({tab}){
    //className에 붙였다가 뗐다가 
    //{tab} State가 변할때마다 하면 될 듯?

  let [fade, setFade] = useState('');

  useEffect( ()=>{
    //end 붙이는 시점을 좀 뒤로
    //react의 automatic batching 기능 
    //state를 변경하는 함수가 근처에 있다면 state변경하는 함수가 여러개면 마지막에만 재 랜더링해준다
    //그래서 시간 지연을 주지 않으면 티가 안난다
    let Timer = setTimeout( ()=>{ setFade('end')}, 100);   
    return () => {
        clearTimeout(Timer);
        setFade('');
    }
  },[tab])
    //class 명에는 띄워쓰기가 있어야 여러개 사용가능
   return (<div className={'start '+ fade}>
    {
        [
            <div>내용0</div>, 
            <div>내용1</div>, 
            <div>내용2</div>    
        ][tab]
    }
   </div>)
}

